import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import accountSelect from '@/components/common-components/accountSelect';
let payAdmin = class payAdmin extends Vue {
    constructor() {
        super(...arguments);
        this.model = {
            phone: '',
            address: '',
            openid: ''
        };
        this.fields = [
            {
                type: 'input',
                modelKey: 'phone',
                label: '手机号',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'textarea',
                modelKey: 'address',
                label: '收货地址',
                props: {
                    placeholder: '示例：乡宁县 新城区 丛林小区331'
                },
                rules: {
                    required: true,
                    message: '请填写正确的收货地址'
                },
                // validating when blur
                trigger: 'blur'
            }
        ];
        this.selectDate = new Date();
        this.raceList = [];
        this.accountId = '';
        this.isShowMore = true;
    }
    // 计算属性
    get formatMonth() {
        return this.format(this.selectDate, 'yyyy-MM');
    }
    activated() {
        // this.pageInit()
        let userInfo = this.$route.query.userInfo;
        if (userInfo.address) {
            this.model.address = userInfo.address;
        }
        if (userInfo.phone) {
            this.model.phone = userInfo.phone;
        }
        this.model.openid = this.$route.query.openid;
    }
    pageInit() {
        let query = this.$route.query;
        this.monthChange(new Date());
    }
    submitHandler(e) {
        e.preventDefault();
        this.$store.dispatch('saveUserInfo', this.model).then((response) => {
            if (response.success) {
                this.$store.commit('COMMODITY_USER_INFO', this.model);
                this.$router.go(-1);
            }
        });
    }
    getRaceListByAccountId() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getRaceListByAccountId', { accountId: this.accountId, start: this.start, end: this.end, page: this.page }).then((response) => {
            if (response.success) {
                let resData = response.data;
                if (response.data.list.length > 0) {
                    this.raceList = this.raceList.concat(resData.list);
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    monthSelect() {
        var that = this;
        this.updatePropsPicker = this.$createDatePicker({
            title: '月份选择',
            max: new Date(),
            value: that.selectDate,
            format: { year: 'YYYY年', month: 'MM月' },
            columnCount: 2,
            onSelect: that.monthChange
        });
        this.updatePropsPicker.show();
    }
    monthChange(date) {
        this.selectDate = date;
        var currentMonth = date.getMonth();
        var nextMonth = ++currentMonth;
        var nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1, '23', '59', '59');
        var oneDay = 1000 * 60 * 60 * 24;
        var end = this.format(new Date(nextMonthFirstDay - oneDay), 'yyyyMMddHHmmss');
        let startDate = new Date(date.getFullYear(), date.getMonth(), date.getDate(), '00', '00', '00');
        startDate.setDate(1);
        var start = this.format(startDate, 'yyyyMMddHHmmss');
        this.start = start;
        this.end = end;
        this.page = 1;
        this.isShowMore = true;
        this.raceList = [];
        this.getRaceListByAccountId();
    }
    showMoreClick() {
        this.page++;
        this.getRaceListByAccountId();
    }
    format(date, format) {
        //eg:format="yyyy-MM-dd hh:mm:ss";
        if (!format) {
            format = 'yyyy-MM-dd hh:mm:ss';
        }
        var o = {
            'M+': date.getMonth() + 1,
            'd+': date.getDate(),
            'H+': date.getHours(),
            'h+': date.getHours(),
            'm+': date.getMinutes(),
            's+': date.getSeconds(),
            'q+': Math.floor((date.getMonth() + 3) / 3),
            'S': date.getMilliseconds()
        };
        if (/(y+)/.test(format)) {
            format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        for (var k in o) {
            if (new RegExp('(' + k + ')').test(format)) {
                format = format.replace(RegExp.$1, RegExp.$1.length === 1
                    ? o[k]
                    : ('00' + o[k]).substr(('' + o[k]).length));
            }
        }
        return format;
    }
};
payAdmin = __decorate([
    Component({
        components: {
            accountSelect
        },
        filters: {
            billType(type) {
                if (type === 'property') {
                    return '物业费用';
                }
                else if (type === 'parking') {
                    return '停车费用';
                }
                else if (type === 'water') {
                    return '水费';
                }
                else {
                    return '其他费用';
                }
            },
            address(item) {
                return item.account_info[0].name + item.build_info[0].name + item.house_info[0].unitNum + '单元' + item.house_info[0].floorNum + '层' + item.house_info[0].roomNum + '室';
            },
            filterEndTime(dateString) {
                var pattern = /(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})(\d{2})/;
                return dateString.replace(pattern, '$1-$2-$3 $4:$5:$6');
            },
            fenToYuan(num) {
                if (typeof num !== 'number' || isNaN(num))
                    return null;
                return '¥' + (num / 100).toFixed(2);
            }
        }
    })
], payAdmin);
export default payAdmin;
